<template>
  <div id="setUser">

    <!-- Form -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="900" draggable >

      <el-form ref="form" :model="form" label-width="100px" class="dialogBox">
        <el-divider content-position="right"> <el-tag>{{ $t('AfterSpu.tagOne') }}</el-tag></el-divider>
        <el-row :gutter="10">
          <el-col :span="12"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.afterOrderNumber')">
              <el-input v-model="form.afterOrderNumber" :readonly="isReadonly"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.problemTitle')">
              <el-select v-model="form.problemId" filterable :placeholder="$t('AfterSpu.problemTitlePlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.problemList" :key="item.id" :label="item.title" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="8"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.spuName')">
              <el-select v-model="form.spuId" @change="handleSelectChange" filterable :placeholder="$t('AfterSpu.spuNamePlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.spuList" :key="item.id" :label="item.spuName" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.function')">
              <el-input v-model="form.functionName" :placeholder="$t('AfterSpu.functionPlaceholder')" :readonly="true"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.series')">
              <el-input v-model="form.seriesName" :placeholder="$t('AfterSpu.seriesPlaceholder')" :readonly="true"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-divider content-position="right"> <el-tag class="ml-2" type="warning">{{ $t('AfterSpu.tagTwo') }}</el-tag></el-divider>
        <el-form-item :label="$t('AfterSpu.disposeUser')" :label-width="formLabelWidth">
          <el-col :span="8">
            <el-input v-model="form.disposeUserNmae" :placeholder="$t('AfterSpu.disposeUserPlaceholder')" :disabled="type == 'alterUser'" autocomplete="off" color="#626aef" >
              <template #append v-if="type != 'alterUser'">
                <el-button icon="Search" @click="handleIconClick"  class="custom-button"></el-button>
              </template>
            </el-input>
          </el-col>
          <el-col :span="1" style="text-align: center;"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="7">
            <el-input :readonly="isReadonly" v-model="form.userRegionName" :placeholder="$t('AfterSpu.disposeRegion')" autocomplete="off" style="width: 100%;"/>
          </el-col>
          <el-col :span="1" style="text-align: center;"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="7">
            <el-input :readonly="isReadonly" v-model="form.roleName" :placeholder="$t('AfterSpu.disposeRole')" autocomplete="off" style="width: 100%;"/>
          </el-col>
        </el-form-item>
        <div class="iconuser" v-if="type == 'alterUser'"><el-icon size="18"><Sort /></el-icon></div>
        <el-form-item :label="$t('AfterSpu.alterUser')" v-if="type == 'alterUser'" :label-width="formLabelWidth">
          <el-col :span="8">
            <el-input v-model="form.newDisposeUserNmae" :placeholder="$t('AfterSpu.disposeUserPlaceholder')" autocomplete="off" color="#626aef" >
              <template #append >
                <el-button icon="Search" @click="handleIconClick" class="custom-button"></el-button>
              </template>
            </el-input>
          </el-col>
          <el-col :span="1" style="text-align: center;"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="7">
            <el-input :readonly="isReadonly" v-model="form.newUserRegionName" :placeholder="$t('AfterSpu.disposeRegion')" autocomplete="off" style="width: 100%;"/>
          </el-col>
          <el-col :span="1" style="text-align: center;"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="7">
            <el-input :readonly="isReadonly" v-model="form.newRoleName" :placeholder="$t('AfterSpu.disposeRole')" autocomplete="off" style="width: 100%;"/>
          </el-col>
        </el-form-item>


        <el-divider content-position="right"><el-tag class="ml-2" type="success">{{ $t('AfterSpu.tagThree') }}</el-tag></el-divider>
        <el-row :gutter="10">
          <el-col :span="12"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.familyName')">
              <el-input v-model="form.familyName" :placeholder="$t('AfterSpu.familyNamePlaceholder')" :readonly="isReadonly"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.summary')">
              <el-input v-model="form.summary" :placeholder="$t('AfterSpu.summaryPlaceholder')" :readonly="isReadonly"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item :label="$t('AfterSpu.email')">
          <el-input v-model="form.email" :placeholder="$t('AfterSpu.emailPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.spuSequence')">
          <el-input v-model="form.spuSequence" :placeholder="$t('AfterSpu.spuSequencePlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.buyTime')" prop="buyTime">
          <el-date-picker v-model="form.buyTime" :placeholder="$t('AfterSpu.buyTimePlaceholder')" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD" placeholder="请输入购买日期" :disabled="isReadonly" style="width: 50%"/>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.disposeUser')" prop="buyDitch">
          <el-col :span="11">
            <el-select filterable v-model="form.buyDitchName" :placeholder="$t('AfterSpu.buyDitchNamePlaceholder')" :disabled="isReadonly" style="width: 100%">
              <el-option v-for="item in selectData.ditchList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
            </el-select>
          </el-col>
          <el-col :span="2" style="text-align: center;"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="11">
            <el-input v-model="form.ditchRemark" :placeholder="$t('AfterSpu.ditchRemarksPlaceholder')" :readonly="isReadonly"/>
          </el-col>
        </el-form-item>
        <el-row :gutter="10">
          <el-col :span="8"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.region')" prop="region">
              <el-select v-model="form.region" filterable :placeholder="$t('AfterSpu.regionPlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.regionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="16"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.prove')">
              <el-input v-model="form.prove" :placeholder="$t('AfterSpu.provePlaceholder')" :readonly="isReadonly"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item :label="$t('AfterSpu.videoUrl')">
          <el-input v-model="form.vedioUrl" :placeholder="$t('AfterSpu.videoUrlPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.detail')">
          <el-input type="textarea" v-model="form.detail" :placeholder="$t('AfterSpu.detailPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.remarks')">
          <el-input type="textarea" v-model="form.remarks" :placeholder="$t('AfterSpu.remarksPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
      </el-form>

      <template #footer>
        <span class="dialog-footer">
          <el-button @click="closeButton()">
            {{ $t('common.button.cancel') }}
          </el-button>
          <el-button type="primary" v-show="type != 'detail'" v-if="type != 'alterUser'" @click="submitButton()"> {{ $t('common.button.confirm') }} </el-button>
          <el-button type="warning" v-show="type != 'detail'" v-if="type == 'alterUser'" @click="careofButton()">{{ $t('common.button.care') }}</el-button>
        </span>
      </template>
    </el-dialog>

    <userAdminSelected ref="userSelected" @childClick="setChooseRow" />

  </div>
</template>

<script>
import spuSelected from "@/views/spuData/spu/spu-selected";
import {getSpuInfoPullDown, getUserAdminPullDown} from "@/http/PullDownApi";
import {getAfterById, updateAfter} from "@/http/spuOred/afterSpu/afterApi";
import {getDictPullDownClientele, getRegionPullDown, listProblemClientele} from "@/http/clienteleApi";
import userAdminSelected from "@/views/basics/user/userAdmin-selected";
import {ElNotification} from "element-plus";
import {saveAfterCareof} from "@/http/spuOred/careofApi";
import {getSysLanguage} from "@/util/system";

export default {
  name: "setUser",
  components: {spuSelected, userAdminSelected},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据
      rowData: {},
      form: {
        spu: {},
        webUrl: '',
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: true,
      formLabelWidth: '100px',

      // 下拉的数据集合对象
      selectData: {},
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
      // newVal - 新值, oldVal - 旧值
      if (newVal == '') return
      if (newVal == 'setUser') this.titleName = this.$t('AfterSpu.appointTitle')
      if (newVal == 'alterUser') this.titleName = this.$t('AfterSpu.transferTitle')
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getPullDownData() {
      // 产品
      getSpuInfoPullDown().then(res => {
        if (res.data.status == 200) this.selectData.spuList = res.data.data;
      }).catch((erro) => { });
      // 售后类型
      listProblemClientele().then(res => {
        if (res.data.status == 200) this.selectData.problemList = res.data.data;
      }).catch((erro) => { });
      // 处理人
      getUserAdminPullDown().then(res => {
        if (res.data.status == 200) this.selectData.userList = res.data.data;
      }).catch((erro) => { });
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.regionList = res.data.data;
      }).catch((erro) => { });
      // 渠道
      getDictPullDownClientele({dictCode: 'GMQD'}).then(res => {
        if (res.data.status == 200) this.selectData.ditchList = res.data.data;
      }).catch((erro) => { });
      this.getRow();
    },
    // 父组件调用 进行初始化
    getRow() {
      getAfterById({
        id: this.rowData.id
      }).then(res => {
        if (res.data.status == 200) this.form = res.data.data;
      }).catch((erro) => {
      });
    },
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },

    handleSelectChange(value) {
      this.selectData.spuList.forEach((item) => {
        if (item.id == value) {
          this.form.functionName = item.functionName;
          this.form.seriesName = item.seriesName;
        }
      });
    },

    // 这里是处理人图标点击事件的处理逻辑
    handleIconClick() {
      this.$refs.userSelected.dialogFormVisible = true;
      this.$refs.userSelected.getPullDownData();
    },
    // 选择处理人后的回调
    setChooseRow(row) {
      let language = getSysLanguage();
      if (this.type == 'setUser') {
        if (language == 'zh-CN') {
          this.form.disposeUserNmae = row.name;
        } else if (language == 'en-US') {
          this.form.disposeUserNmae = row.nickName;
        }
        this.form.userRegionName = row.regionName;
        this.form.roleName = row.roleName;
        this.form.disposeUser = row.id;
      }
      if (this.type == 'alterUser') {
        if (language == 'zh-CN') {
          this.form.newDisposeUserNmae = row.name;
        } else if (language == 'en-US') {
          this.form.newDisposeUserNmae = row.nickName;
        }
        this.form.newUserRegionName = row.regionName;
        this.form.newRoleName = row.roleName;
        this.form.newDisposeUser = row.id;

      }
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible = false;
      this.isReadonly = true;
      this.form = {};
      this.rowData = {};
    },
    // 确认按钮
    submitButton() {
      if (this.form.disposeUser == null || this.form.disposeUser == '') {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('AfterSpu.disposeUserError'), type: 'warning',})
        return
      }
      updateAfter(this.form).then(res => {
        if (res.data.status == 200) {
          ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
          // 刷新列表
          this.$emit('operateClick');
          this.closeButton();
        }
      }).catch((erro) => {});
    },
    // 转交按钮
    careofButton() {
      if (this.form.newDisposeUser == null || this.form.newDisposeUser == '') {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('AfterSpu.alterUserError'), type: 'warning',})
        return
      }
      let data = {
        afterOrderNumber: this.form.afterOrderNumber,
        agoUser: this.form.disposeUser,
        nowUser: this.form.newDisposeUser,
      }
      saveAfterCareof(data).then(res => {
        if (res.data.status == 200) {
          ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
          // 刷新列表
          this.$emit('operateClick');
          this.closeButton();
        }
      }).catch((erro) => {});
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.dialogBox {
  overflow-y: auto;
  height: 500px;
}
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}

/deep/ .el-dialog__body {
  padding-top: 5px;
}

/* 输入框后面图标的样式 */
.el-input-group__append button.el-button {
  border-color: white;
  background-color: #409eff;
  color: #eaeaef;
}

.iconuser{
/*居中加粗*/
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  color: #409eff;
  margin-top: 5px;
  margin-bottom: 5px;
}
</style>
