<template>
  <div id="flow">
    <el-drawer v-model="drawer" :direction="direction" @close="closeDrawer">
      <template #header>
        <div class="titleBox">
          {{ $t('AfterSpu.flow.title') }} {{form.afterOrderNumber}} -
          <el-tag v-show="form.afterStatus == 1">{{ $t('pullDown.afterStatus.allocation') }}</el-tag>
          <el-tag class="ml-2" v-show="form.afterStatus == 2" type="success">{{ $t('pullDown.afterStatus.pending') }}</el-tag>
          <el-tag class="ml-2" v-show="form.afterStatus == 3" type="warning">{{ $t('pullDown.afterStatus.processing') }}</el-tag>
          <el-tag class="ml-2" v-show="form.afterStatus == 4" type="info">{{ $t('pullDown.afterStatus.completed') }}</el-tag>
          <el-tag class="ml-2" v-show="form.afterStatus == 5" type="danger">{{ $t('pullDown.afterStatus.unknown') }}</el-tag>
        </div>
      </template>
      <template #default>

        <div class="flow">
          <el-card class="box-card" shadow="hover">
            <div class="textBox">
              <el-timeline :reverse="false">
                <el-timeline-item v-for="item in afterFlowVoList" :key="item.id"  :timestamp="newStr(item)" placement="top">
                  <el-card shadow="hover">
                    <div class="titlebox">
                      {{ $t('AfterSpu.flow.summarize') }} {{ item.title }}
                      <div class="fromBox" >
                        <el-button v-if="item.affairId != null && item.affairDetailId == null" @click="addAffair(item)" key="primary" type="primary" link>
                          {{ $t('common.button.fillIn') }}
                        </el-button>
                        <el-button v-if="item.affairDetailId != null" @click="getAffair(item.affairDetailId)" key="primary" type="primary" link>
                          {{ $t('common.button.check') }}
                        </el-button>
                      </div>
                    </div>
                    <div class="time">
                      <b>{{ $t('AfterSpu.flow.describe') }}</b>{{ item.content }}
                    </div>
                    <div class="demo-image__preview" v-if="item.imgs != null">
                      <el-divider content-position="right">{{ $t('AfterSpu.flow.reference') }}</el-divider>
                      <el-image v-for="img in item.imgs" :key="img"
                                style="width: 85px; height: 85px; margin-right: 5px; margin-bottom: 5px;"
                                :src="getUrl(img)" :zoom-rate="1.2" :max-scale="7" :min-scale="0.2"
                                :preview-src-list="addurl(item.imgs)" :initial-index="4" fit="cover"
                      />
                    </div>
                  </el-card>
                </el-timeline-item>
              </el-timeline>
            </div>
          </el-card>
        </div>
      </template>
      <template #footer>
        <div style="flex: auto">
          <div style="float: left">
            <el-button type="warning" plain @click="gotoAffair()">{{ $t('common.button.record') }}</el-button>
            <el-button type="success" plain @click="newAffair()" v-show="form.afterStatus != 4">{{ $t('common.button.after') }}</el-button>
          </div>
          <el-button type="primary" @click="writeBack()" v-show="form.afterStatus != 4">{{ $t('common.button.reply') }}</el-button>
          <el-button @click="cancelClick">{{ $t('common.button.close') }}</el-button>
          <el-button type="danger" @click="updateAfterStatus()" v-show="form.afterStatus != 4">{{ $t('common.button.end') }}</el-button>
        </div>
      </template>
    </el-drawer>
  </div>

  <!-- 流程物流信息查询 -->
  <el-dialog v-model="selecAffairVisible" :before-close="fillInBeforeClose" :title="$t('common.dialog.fillInLogisticsTitle')" width="750" draggable>
    <el-form :rules="fillInRules" ref="fillInForm" :model="selectAffairForm" label-width="80px">
      <el-form-item :label="$t('AfterSpu.afterOrderNumber')">
        <el-input v-model="selectAffairForm.afterOrderNumber" :readonly="true"></el-input>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.ditchStatus')" prop="ditchStatus">
        <el-select filterable v-model="selectAffairForm.ditchStatus" :placeholder="$t('AfterSpu.flow.ditchStatusPlaceholder')" :disabled="true" style="width: 100%">
          <el-option v-for="item in selectData.ditchStatusList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.logistics')" prop="expressage">
        <el-input v-model="selectAffairForm.expressage" :placeholder="$t('AfterSpu.flow.logisticsPlaceholder')" :readonly="isReadonly"></el-input>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.logisticsNumber')" prop="expressageNumber">
        <el-input v-model="selectAffairForm.expressageNumber" :placeholder="$t('AfterSpu.flow.logisticsNumberPlaceholder')" :readonly="isReadonly"></el-input>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.sendoutDate')" prop="sendoutDate">
        <el-date-picker v-model="selectAffairForm.sendoutDate" :placeholder="$t('AfterSpu.flow.sendoutDatePlaceholder')" :disabled="isReadonly" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD" placeholder="请输入购买日期" style="width: 100%"/>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.remarks')">
        <el-input type="textarea" v-model="selectAffairForm.remarks" :placeholder="$t('AfterSpu.flow.remarksPlaceholder')" :readonly="isReadonly"></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="selectAffairClick">{{ $t('common.button.back') }}</el-button>
        <el-button type="primary" @click="selectAffairAdd" v-if="!isReadonly">{{ $t('common.button.submit') }}</el-button>
      </span>
    </template>
  </el-dialog>

  <!-- 新建工单事件 -->
  <el-dialog v-model="newAffairVisible" :before-close="eventBeforeClose" :title="$t('common.dialog.eventTitle')" width="750" draggable>
    <el-form :rules="eventRules" ref="eventForm" :model="newAffairForm" label-width="80px">
      <el-form-item :label="$t('AfterSpu.afterOrderNumber')">
        <el-input v-model="newAffairForm.afterOrderNumber" :readonly="true"></el-input>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.ditchName')" prop="ditchName">
        <el-select filterable v-model="newAffairForm.ditchName" :placeholder="$t('AfterSpu.flow.ditchNamePlaceholder')" style="width: 100%">
          <el-option v-for="item in selectData.ditchNameList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.newDitchStatus')" prop="ditchStatus">
        <el-select filterable v-model="newAffairForm.ditchStatus" :disabled="true" :placeholder="$t('AfterSpu.flow.newDitchStatusPlaceholder')" style="width: 100%">
          <el-option v-for="item in selectData.ditchStatusList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.newRemarks')">
        <el-input type="textarea" v-model="newAffairForm.remarks" :placeholder="$t('AfterSpu.flow.newRemarksPlaceholder')"></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="newAffairClick">{{ $t('common.button.back') }}</el-button>
        <el-button type="primary" @click="newAffairAdd">
          {{ $t('common.button.submit') }}
        </el-button>
      </span>
    </template>
  </el-dialog>

  <!-- 回复 弹出框 -->
  <el-dialog v-model="dialogFormVisible" :before-close="replyBeforeClose" :title="$t('common.dialog.replyTitle')" width="900" draggable>
    <el-form ref="replyForm" :rules="replyRules" :model="form" label-width="80px">
      <el-form-item :label="$t('AfterSpu.afterOrderNumber')">
        <el-input v-model="form.afterOrderNumber" :readonly="true"></el-input>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.formTitle')" prop="title">
        <el-input v-model="form.title" :placeholder="$t('AfterSpu.flow.formTitlePlaceholder')"></el-input>
      </el-form-item>
      <el-form-item :label="$t('AfterSpu.flow.formDescribe')" prop="content" >
        <el-input v-model="form.content" :rows="5" type="textarea" :placeholder="$t('AfterSpu.flow.formDescribePlaceholder')"/>
      </el-form-item>
      <el-divider content-position="left">{{ $t('AfterSpu.flow.uploadTitle') }}</el-divider>
      <div class="imgdiv">
        <el-upload
            v-model:file-list="form.fileList"
            action="/api/clientele/uploadFileClientele"
            :data="fileData" list-type="picture-card"
            :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload"
            :on-preview="handlePictureCardPreview" :on-remove="handleRemove"
        >
          <el-icon><Plus /></el-icon>
        </el-upload>

        <el-dialog v-model="dialogVisible">
          <img w-full :src="dialogImageUrl" alt="Preview Image" />
        </el-dialog>
      </div>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="wordCancelClick">{{ $t('common.button.back') }}</el-button>
        <el-button type="primary" @click="wordConfirmClick">
          {{ $t('common.button.submit') }}
        </el-button>
      </span>
    </template>
  </el-dialog>

  <!-- 记录 弹出框 -->
  <afterSpu-affair ref="affair" @childClick="affairAndNew"/>
</template>

<script>


import {ElLoading, ElNotification} from "element-plus";
import {getAffairDetailByIdClientele, getDictPullDownClientele, saveAffairClientele,} from "@/http/clienteleApi";
import {finishAfter, listAfterFlowBySelcetUrl, saveAfterFlow} from "@/http/spuOred/afterSpu/afterApi";
import {saveAffair} from "@/http/spuOred/afterSpu/affairApi";
import afterSpuAffair from "@/views/spuOred/afterSpu/afterSpu-affair";

export default {
  name: "flow",
  components: {afterSpuAffair},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      drawer: false,
      direction: 'rtl',

      // 售后工单物流查询
      selecAffairVisible: false,
      isReadonly: false,
      selectAffairForm: {
        affairId: 0,
        affairDetailId: 0,
        afterOrderNumber: '',
        ditchStatus: 'LCZT-002',
        expressage: '',
        expressageNumber: '',
        sendoutDate: '',
        remarks: '',
      },
      // 物流框 - 表单验证规则
      fillInRules: {
        ditchStatus: [
          {required: true, message: this.$t('AfterSpu.flow.ditchStatusPlaceholder'), trigger: 'change'},
        ],
        expressage: [
          {required: true, message: this.$t('AfterSpu.flow.logisticsPlaceholder'), trigger: 'blur'},
        ],
        expressageNumber: [
          {required: true, message: this.$t('AfterSpu.flow.logisticsNumberPlaceholder'), trigger: 'blur'},
        ],
        sendoutDate: [
          {required: true, message: this.$t('AfterSpu.flow.sendoutDatePlaceholder'), trigger: 'change'},
        ],
      },

      // 售后工单新增事件
      newAffairVisible: false,
      selectData: {
        ditchNameList: [],
        ditchStatusList: [],
      },
      newAffairForm: {
        afterOrderNumber: '',
        ditchName: '',
        ditchStatus: 'LCZT-001',
        remarks: '',
      },
      // 事件框 - 表单验证规则
      eventRules: {
        ditchName: [
          {required: true, message: this.$t('AfterSpu.flow.ditchNamePlaceholder'), trigger: 'change'},
        ],
        ditchStatus: [
          {required: true, message: this.$t('AfterSpu.flow.newDitchStatusPlaceholder'), trigger: 'change'},
        ],
      },

      dialogFormVisible: false,
      formLabelWidth: '100px',

      afterFlowVoList: {}, // 产品信息
      form: {
        fileList: [],
      },
      // 回复框 - 表单验证规则
      replyRules: {
        title: [
          {required: true, message: this.$t('AfterSpu.flow.formTitlePlaceholder'), trigger: 'blur'},
        ],
        content: [
          {required: true, message: this.$t('AfterSpu.flow.formDescribePlaceholder'), trigger: 'blur'},
        ],
      },
      // 请求接口需要携带的参数
      fileData: {
        folder: 'flow-img'
      },
      dialogImageUrl: '',
      dialogVisible: false,
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {

  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getPullDownData();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getPullDownData() {

    },
    addurl(imgs) {
      if (!imgs) return [];
      let arr = [];
      imgs.forEach(item => {
        arr.push(this.getUrl(item));
      })
      return arr;
    },
    newStr(item) {
      return (item.createStatus == 1 ? this.$t('AfterSpu.flow.user') + ' ' + item.familyName : this.$t('AfterSpu.flow.service') + item.disposeUserNmae) + ' ' + item.createTime
    },
    affairAndNew() {
      this.getRow(this.form.selcetUrl);
    },
    // 父组件调用 进行初始化
    getRow(selcetUrl) {
      this.form.selcetUrl = selcetUrl;
      listAfterFlowBySelcetUrl({
        selcetUrl: selcetUrl
      }).then(res => {
        if (res.data.status == 200) {
          this.afterFlowVoList = res.data.data.afterFlowVoList;
        }
      }).catch((erro) => { this.reset(); });
    },
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },

    //  回复按钮
    writeBack () {
      this.dialogFormVisible = true;
    },
    // 取消按钮
    cancelClick() {
      this.drawer = false;
      this.form = {};
      this.afterFlowVoList = {};
      this.$emit('operateClick');
    },
    // 结束售后工单
    updateAfterStatus() {
      this.$confirm(this.$t('ClienteleQuery.finishTxt'), this.$t('common.dialog.title'),
          {confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'}
      ).then(() => {
        // 结束售后工单请求
        finishAfter(this.form).then(res => {
          if (res.data.status == 200) {
            ElNotification({title: this.$t('common.message.successTitle'), message: this.$t('AfterSpu.flow.endTag'), type: 'success',})
            this.cancelClick();
          }
        }).catch(error => { });
      }).catch(() => { });
    },
    closeDrawer() {
      this.cancelClick();
      // 调用父组件的方法
      this.$emit('operateClick');
    },

    // ---------- 物流查询按钮 -----------
    getaffData() {
      // 事件状态
      getDictPullDownClientele({dictCode: 'LCZT'}).then(res => {
        if (res.data.status == 200) this.selectData.ditchStatusList = res.data.data;
      }).catch((erro) => { });
      this.selectAffairForm.afterOrderNumber = this.form.afterOrderNumber;
      this.selecAffairVisible = true;
    },
    // 打开填入物流弹窗
    addAffair(item) {
      this.isReadonly = false;
      this.selectAffairForm.affairId = item.affairId;
      this.selectAffairForm.flowId = item.id;
      this.selectAffairForm.expressage = '';
      this.selectAffairForm.expressageNumber = '';
      this.selectAffairForm.sendoutDate = '';
      this.selectAffairForm.remarks = '';
      this.getaffData();
    },
    // 打开查看物流弹窗
    getAffair(affairDetailId) {
      this.selectAffairForm.affairDetailId = affairDetailId;
      this.getaffData();
      this.isReadonly = true;
      // 根据id查询物流信息
      getAffairDetailByIdClientele({affairDetailId: affairDetailId}).then(res => {
        if (res.data.status == 200) {
          this.selectAffairForm = res.data.data;
          this.selectAffairForm.afterOrderNumber = this.form.afterOrderNumber;
        }
      }).catch((erro) => { });
    },
    // 提交用户的物流信息
    selectAffairAdd() {
      // 提交校验
      this.$refs.fillInForm.validate((valid) => {
        if (valid) {
          const loading = ElLoading.service({
            lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
          );
          this.selectAffairForm.expressageStatus = 1;
          this.selectAffairForm.createStatus = 2;
          saveAffairClientele(this.selectAffairForm).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',});
              this.selectAffairClick();
              this.getRow(this.form.selcetUrl);
            }
          }).finally(() => {
            // 请求无论成功/失败 都执行关闭loading
            loading.close();
          });
        } else {
          return false;
        }
      });
    },
    // 点击遮罩和x时执行
    fillInBeforeClose(done) {
      this.selectAffairClick();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 关闭
    selectAffairClick() {
      this.selecAffairVisible = false;
      this.isReadonly = false;
      this.selectAffairForm = {
        affairId: 0,
        affairDetailId: 0,
        afterOrderNumber: '',
        ditchStatus: 'LCZT-002',
        expressage: '',
        expressageNumber: '',
        sendoutDate: '',
        remarks: '',
      };
      this.$refs.fillInForm.resetFields(); // 重置表单验证
    },
    // 打开记录详情页面
    gotoAffair() {
      this.$refs.affair.dialogFormVisible = true;
      this.$refs.affair.row = {
        afterOrderNumber: this.form.afterOrderNumber
      };
      this.$refs.affair.gotoExecute();
    },

    // ------------ 售后按钮 打开新增售后工单页面 ------------
    newAffair() {
      // 事件名称
      getDictPullDownClientele({dictCode: 'SHSJ'}).then(res => {
        if (res.data.status == 200) this.selectData.ditchNameList = res.data.data;
      }).catch((erro) => { });
      // 事件状态
      getDictPullDownClientele({dictCode: 'LCZT'}).then(res => {
        if (res.data.status == 200) this.selectData.ditchStatusList = res.data.data;
      }).catch((erro) => { });
      this.newAffairForm.afterOrderNumber = this.form.afterOrderNumber;
      this.newAffairVisible = true;
      this.newAffairForm.ditchStatus = 'LCZT-001';
    },
    // 新增售后工单的事件
    newAffairAdd() {
      // 提交校验
      this.$refs.eventForm.validate((valid) => {
        if (valid) {
          const loading = ElLoading.service({
            lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
          );
          this.newAffairForm.webUrl = process.env.VUE_APP_BACKEND_WEB_URL + "/selectAfter/";
          saveAffair(this.newAffairForm).then(res => {
            if (res.data.status == 200) {
              this.newAffairVisible = false;
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',});
              this.newAffairClick();
              this.getRow(this.form.selcetUrl);
            }
          }).finally(() => {
            // 请求无论成功/失败 都执行关闭loading
            loading.close();
          });
        } else {
          return false;
        }
      });
    },
    // 点击遮罩和x时执行
    eventBeforeClose(done) {
      this.newAffairClick();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 关闭
    newAffairClick() {
      this.newAffairVisible = false;
      this.newAffairForm = {
        afterOrderNumber: '',
        ditchName: '',
        ditchStatus: 'LCZT-001',
        remarks: '',
      };
      this.$refs.eventForm.resetFields(); // 重置表单验证
    },


    // -------- 下面为 输入弹窗的方法 ---------
    // 点击遮罩和x时执行
    replyBeforeClose(done) {
      this.wordCancelClick();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    wordCancelClick() {
      this.dialogFormVisible = false;
      this.form = {
        afterOrderNumber: this.form.afterOrderNumber,
        title: '',
        content: '',
        fileList: [],
      };
      this.$refs.replyForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    wordConfirmClick() {
      // 提交校验
      this.$refs.replyForm.validate((valid) => {
        if (valid) {
          const loading = ElLoading.service({
            lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
          );
          if (this.form.fileList && this.form.fileList.length > 0) {
            let imgs = [];
            for (let i = 0; i < this.form.fileList.length; i++) {
              imgs.push(this.form.fileList[i].response.data);
            }
            this.form.imgs = imgs;
          }
          this.form.createStatus = 2;
          this.form.webUrl = process.env.VUE_APP_BACKEND_WEB_URL + "/selectAfter/";
          // 提交表单
          saveAfterFlow(this.form).then(res => {
            if (res.data.status == 200) {
              this.dialogFormVisible = false;
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
              this.form.title = '';
              this.form.content = '';
              this.form.fileList = [];
              this.getRow(this.form.selcetUrl);
            }
          }).finally(() => {
            // 请求无论成功/失败 都执行关闭loading
            loading.close();
          });
        } else {
          return false;
        }
      });
    },
    // 上传之后的res结果集
    handleAvatarSuccess(res, file) {
      if (res.status == 200) {
        // vue.config.js 定义了后端请求前缀路径 可直接调用
        this.imageUrl = process.env.VUE_APP_BACKEND_API_URL + res.data;
      } else {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.networkError'), type: 'info',})
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadError'), type: 'info',})
      }
      if (!isLt2M) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadSizeError'), type: 'info',})
      }
      return isJPG && isLt2M;
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },

  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}
.flow {
  width: 100%;
  height: 98%;
  margin-bottom: 15px;
}
