import request from '../../request'


// 新增
export function saveAffair(data) {
    return request({
        url: '/api/affair/saveAffair',
        method: 'post',
        data
    })
}

// 查询
export function listAffair(data) {
    return request({
        url: '/api/affair/listAffair',
        method: 'post',
        data
    })
}

// 新增事件流程
export function saveAffairDetail(data) {
    return request({
        url: '/api/affair/saveAffairDetail',
        method: 'post',
        data
    })
}

// 根据事件id获取未签收的物流
export function getAffairDetailByIdAndExp(params) {
    return request({
        url: '/api/affair/getAffairDetailByIdAndExp',
        method: 'get',
        params: params
    });
}

// 修改
export function updateAffair(data) {
    return request({
        url: '/api/affair/updateAffair',
        method: 'post',
        data
    })
}
