<template>
  <div id="style-operate">

    <!-- Form -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="900" draggable >

      <el-form :rules="rules" ref="ruleForm" :model="form" label-width="100px" class="dialogBox">
        <el-divider content-position="right"> <el-tag>{{ $t('AfterSpu.tagOne') }}</el-tag></el-divider>
        <el-row :gutter="10"  v-if="type != 'add'">
          <el-col :span="12"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.afterOrderNumber')">
              <el-input v-model="form.afterOrderNumber" :readonly="true"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.problemTitle')" prop="problemId">
              <el-select v-model="form.problemId" filterable :placeholder="$t('AfterSpu.problemTitlePlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.problemList" :key="item.id" :label="item.title" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item :label="$t('AfterSpu.problemTitle')" prop="problemId" v-if="type == 'add'">
          <el-select v-model="form.problemId" filterable :placeholder="$t('AfterSpu.problemTitlePlaceholder')" :disabled="isReadonly" style="width: 100%">
            <el-option v-for="item in selectData.problemList" :key="item.id" :label="item.title" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-row :gutter="10">
          <el-col :span="8"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.spuName')" prop="spuId">
              <el-select v-model="form.spuId" @change="handleSelectChange" filterable :placeholder="$t('AfterSpu.spuNamePlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.spuList" :key="item.id" :label="item.spuName" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.function')">
              <el-input v-model="form.functionName" :placeholder="$t('AfterSpu.functionPlaceholder')" :readonly="true"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.series')">
              <el-input v-model="form.seriesName" :placeholder="$t('AfterSpu.seriesPlaceholder')" :readonly="true"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-divider content-position="right" v-if="type == 'detail'"> <el-tag class="ml-2" type="warning" >{{ $t('AfterSpu.tagTwo') }}</el-tag></el-divider>
        <el-form-item :label="$t('AfterSpu.disposeUser')" :label-width="formLabelWidth" v-if="type == 'detail'">
          <el-col :span="10">
            <el-input :readonly="isReadonly" v-model="form.disposeUserNmae" :placeholder="$t('AfterSpu.disposeUserPlaceholder')" autocomplete="off" style="width: 100%;"/>
          </el-col>
          <el-col :span="1" style="text-align: center;"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="6">
            <el-input :readonly="isReadonly" v-model="form.userRegionName" :placeholder="$t('AfterSpu.disposeRegion')" autocomplete="off" style="width: 100%;"/>
          </el-col>
          <el-col :span="1" style="text-align: center;"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="6">
            <el-input :readonly="isReadonly" v-model="form.roleName" :placeholder="$t('AfterSpu.disposeRole')" autocomplete="off" style="width: 100%;"/>
          </el-col>
        </el-form-item>

        <el-divider content-position="right"><el-tag class="ml-2" type="success">{{ $t('AfterSpu.tagThree') }}</el-tag></el-divider>
        <el-row :gutter="10">
          <el-col :span="12"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.familyName')" prop="familyName">
              <el-input v-model="form.familyName" :placeholder="$t('AfterSpu.familyNamePlaceholder')" :readonly="isReadonly"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.summary')" prop="summary">
              <el-input v-model="form.summary" :placeholder="$t('AfterSpu.summaryPlaceholder')" :readonly="isReadonly"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item :label="$t('AfterSpu.email')" prop="email">
          <el-input v-model="form.email" :placeholder="$t('AfterSpu.emailPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.spuSequence')" prop="spuSequence">
          <el-input v-model="form.spuSequence" :placeholder="$t('AfterSpu.spuSequencePlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.buyTime')" prop="buyTime">
          <el-date-picker v-model="form.buyTime" :placeholder="$t('AfterSpu.buyTimePlaceholder')" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD" placeholder="请输入购买日期" :disabled="isReadonly" style="width: 50%"/>
        </el-form-item>

        <el-form-item :label="$t('AfterSpu.buyDitchName')" required>
          <el-col :span="11">
            <el-form-item prop="buyDitchName">
              <el-select filterable v-model="form.buyDitchName" :placeholder="$t('AfterSpu.buyDitchNamePlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.ditchList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="2" style="text-align: center;"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="11">
            <el-form-item prop="ditchRemark">
              <el-input v-model="form.ditchRemark" :placeholder="$t('AfterSpu.ditchRemarksPlaceholder')" :readonly="isReadonly"/>
            </el-form-item>
          </el-col>
        </el-form-item>

        <el-row :gutter="10">
          <el-col :span="8"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.region')" prop="region">
              <el-select v-model="form.region" filterable :placeholder="$t('AfterSpu.regionPlaceholder')" :disabled="isReadonly" style="width: 100%">
                <el-option v-for="item in selectData.regionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="16"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('AfterSpu.prove')">
              <el-input v-model="form.prove" :placeholder="$t('AfterSpu.provePlaceholder')" :readonly="isReadonly"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item :label="$t('AfterSpu.videoUrl')">
          <el-input v-model="form.vedioUrl" :placeholder="$t('AfterSpu.videoUrlPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.detail')"  prop="detail">
          <el-input type="textarea" v-model="form.detail" :placeholder="$t('AfterSpu.detailPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('AfterSpu.remarks')">
          <el-input type="textarea" v-model="form.remarks" :placeholder="$t('AfterSpu.remarksPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>

      </el-form>

      <template #footer>
        <span class="dialog-footer">
          <el-button @click="closeButton()">
            {{ $t('common.button.cancel') }}
          </el-button>
          <el-button type="primary" v-show="type != 'detail'" @click="submitButton()"> {{ $t('common.button.confirm') }} </el-button>
        </span>
      </template>
    </el-dialog>

    <spu-Selected ref="selected" @childClick="setList"/>
  </div>
</template>

<script>
import {ElNotification} from "element-plus";
import spuSelected from "@/views/spuData/spu/spu-selected";
import {getSpuInfoPullDown, getUserAdminPullDown} from "@/http/PullDownApi";
import {getAfterById, saveAfter, updateAfter} from "@/http/spuOred/afterSpu/afterApi";
import {getDictPullDownClientele, getRegionPullDown, listProblemClientele} from "@/http/clienteleApi";

export default {
  name: "style-operate",
  components: {spuSelected},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据
      rowData: {},
      // 行数据副本
      form: {
        spu: { },
        webUrl: '',
      },
      // 表单验证规则
      rules: {
        problemId: [
          {required: true, message: this.$t('AfterSpu.problemTitlePlaceholder'), trigger: 'change'},
        ],
        spuId: [
          {required: true, message: this.$t('AfterSpu.spuNamePlaceholder'), trigger: 'change'},
        ],
        familyName: [
          {required: true, message: this.$t('AfterSpu.familyNamePlaceholder'), trigger: 'blur'},
        ],
        summary: [
          {required: true, message: this.$t('AfterSpu.summaryPlaceholder'), trigger: 'blur'},
        ],
        email: [
          {required: true, message: this.$t('AfterSpu.emailPlaceholder'), trigger: 'blur'},
        ],
        spuSequence: [
          {required: true, message: this.$t('AfterSpu.spuSequencePlaceholder'), trigger: 'blur'},
        ],
        buyTime: [
          {required: true, message: this.$t('AfterSpu.buyTimePlaceholder'), trigger: 'change'},
        ],
        buyDitchName: [
          {required: true, message: this.$t('AfterSpu.buyDitchNamePlaceholder'), trigger: 'change'},
        ],
        ditchRemark: [
          {required: true, message: this.$t('AfterSpu.ditchRemarksPlaceholder'), trigger: 'blur'},
        ],
        region: [
          {required: true, message: this.$t('AfterSpu.regionPlaceholder'), trigger: 'change'},
        ],
        detail: [
          {required: true, message: this.$t('AfterSpu.detailPlaceholder'), trigger: 'blur'},
        ],
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      formLabelWidth: '100px',

      // 下拉的数据集合对象
      selectData: { },
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
      if (newVal == '') return
      if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
      if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
      if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getPullDownData();
  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getPullDownData() {
      // 产品
      getSpuInfoPullDown().then(res => {
        if (res.data.status == 200) this.selectData.spuList = res.data.data;
      }).catch((erro) => { });
      // 售后类型
      listProblemClientele().then(res => {
        if (res.data.status == 200) this.selectData.problemList = res.data.data;
      }).catch((erro) => { });
      // 处理人
      getUserAdminPullDown().then(res => {
        if (res.data.status == 200) this.selectData.userList = res.data.data;
      }).catch((erro) => { });
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.regionList = res.data.data;
      }).catch((erro) => { });
      // 渠道
      getDictPullDownClientele({dictCode: 'GMQD'}).then(res => {
        if (res.data.status == 200) this.selectData.ditchList = res.data.data;
      }).catch((erro) => { });
    },
    // 父组件调用 进行初始化
    getRow(id) {
      getAfterById({
        id: id
      }).then(res => {
        if (res.data.status == 200) this.form = res.data.data;
      }).catch((erro) => {
      });
    },
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },

    handleSelectChange(value) {
      this.selectData.spuList.forEach((item) => {
        if (item.id == value) {
          this.form.functionName = item.functionName;
          this.form.seriesName = item.seriesName;
        }
      });
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible = false;
      this.isReadonly = false;
      this.form = {};
      this.rowData = {};
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    submitButton() {
      // 提交校验
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.type == "add") {
            this.form.webUrl = process.env.VUE_APP_BACKEND_WEB_URL + "/selectAfter/";;
            saveAfter(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible = false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => {loading.close();});
          } else if (this.type == "edit") {
            updateAfter(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible = false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            });
          }
          this.closeButton();
        } else {
          return false;
        }
      });
    },

    // 列表删除
    deleteRow(index, row) {
      // 删除前端渲染数组的数据
      this.form.detailList.splice(index, 1);
    },

    // 选入按钮
    selected() {
      this.$refs.selected.dialogFormVisible = true; // 开启选入框
      // 传入已选择的数据 用于回显
      this.$refs.selected.setDataList = this.form.detailList == null ? null : JSON.parse(JSON.stringify(this.form.detailList));
      this.$refs.selected.getPullDownData(); // 执行选入框初始化方法
    },
    // 选入确认后 将数据更新和载入
    setList(list) {
      // 操作新的选中的数据
      list.forEach((item) => {
        item.quantity = 1;
      });
      // 新数据如果存在已有的数据id则清除掉 只留下已有数据不存在的数据 然后跟已有的数据追加合并
      this.form.detailList = list;
    }
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.dialogBox {
  overflow-y: auto;
  height: 500px;
}
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}
/deep/ .el-dialog__body {
  padding-top: 5px;
}
</style>
